export const query = graphql`
  query indexQuery {
    allMediumPost(limit: 4, sort: { fields: [createdAt], order: DESC }) {
      edges {
        node {
          title
          virtuals {
            subtitle
            previewImage {
              imageId
            }
          }
          author {
            name
            imageId
          }
          medium_id
          createdAt(fromNow: true)
        }
      }
    }
    allStrapiEvent(limit: 1, filter: { announced: { eq: true } }) {
      edges {
        node {
          id
          title
          imageFb {
            childImageSharp {
              original {
                src
              }
            }
          }
          imageIg {
            childImageSharp {
              original {
                src
              }
            }
          }
          startTime
          ticketUrl
        }
      }
    }
  }
`

export default IndexPage
